import { GET_AVATAR, HIDE_MODAL, LOGIN_MODAL, REGISTER_MODAL } from './constant'

export const getAvatar = avatar => {
    return {
        type: GET_AVATAR,
        payload: avatar
    }
}

export const loginModal = () => {
    return {
        type: LOGIN_MODAL
    }
}

export const registerModal = () => {
    return {
        type: REGISTER_MODAL
    }
}

export const hideModal = () => {
    return {
        type: HIDE_MODAL
    }
}
